.article-detail {
  padding: 20px calc(50% - 600px);
  min-height: calc(100vh - 64px - 62px);
}

/*文章内容*/
.article-cont {
  background-color: #ffffff;
  padding: 0 30px;

  &--title {
    font-size: 24px;
    margin: 20px 0;
    color: #333;
  }
  &--author {
    color: #999;

    .author01 {
      margin-right: 5px;
      font-size: 18px;
    }
    .author02 {
      margin-left: 15px;
      margin-right: 5px;
      font-size: 12px;
    }
  }
  &--about {
    color: #888888;
    border: 1px solid #F3F3F3;
    padding: 10px;
    margin: 20px auto 15px auto;
    line-height: 23px;
    background: none repeat 0 0 #F6F6F6;

    strong {
      color: #38485A;
      font-weight: 400 !important;
      font-size: 13px;
      padding-right: 8px;
    }
  }

}

/*内容全部样式*/
.article-markdown {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 1.05rem;
  color: #777;
  line-height: 1.9rem;
  word-wrap: break-word;

  * {
    box-sizing: border-box;
  }

  h1, h2, h3, h4, h5, h6 {
    margin-top: 24px;
    margin-bottom: 16px;
    font-weight: 600;
    line-height: 1.25;
  }
  h1 {
    margin: .67em 0;
    padding-bottom: .3em;
    font-size: 2em;
    border-bottom: 1px solid #eaecef;
  }
  h2 {
    padding-bottom: .3em;
    font-size: 1.5em;
    border-bottom: 1px solid #eaecef;
  }
  h3 {
    font-size: 1.25em;
  }
  h4 {
    font-size: 1em;
  }
  h5 {
    font-size: .875em;
  }
  h6 {
    font-size: .85em;
    color: #6a737d;
  }
  strong {
    font-weight: 600;
  }
  blockquote,
  dl,
  ol,
  p,
  pre,
  table,
  ul {
    margin-top: 0;
    margin-bottom: 16px;
  }
  .hljs-left {
    text-align: left;
  }
  .hljs-center {
    text-align: center;
  }
  .hljs-right {
    text-align: right;
  }
  blockquote {
    padding: 0 1em;
    color: #6a737d;
    border-left: .25em solid #dfe2e5;
  }
  ol, ul {
    padding-left: 2em;
  }
  a {
    color: #0366d6;
    text-decoration: none;background-color: transparent;
    -webkit-text-decoration-skip: objects;
  }
  img {
    max-width: 100%;
    box-sizing: content-box;
    background-color: #fff;
    border-style: none;
  }
  .highlight pre,
  pre {
    display: block;
    background-color: #283646 !important;
    padding: .5rem !important;
    overflow-y: auto;
    font-weight: 300;
    font-family: Menlo, monospace;
    border-radius: .3rem;

    code {
      border: 0 !important;
      background-color: #283646 !important;
      border-radius: 3px;
      padding-left: 5px;
      padding-right: 5px;
      margin: 0 3px;
      line-height: 1.1rem;
      display: inline-block;
      color: #ccc !important;
      font-size: .87rem;
    }
  }
  table {
    display: block;
    width: 100%;
    overflow: auto;
    border-spacing: 0;
    border-collapse: collapse;

    tr {
      background-color: #fff;
      border-top: 1px solid #c6cbd1;
    }
    td, th {
      padding: 6px 13px;
      border: 1px solid #dfe2e5;
    }
    th {
      font-weight: 600;
    }
  }
  mark {
    background-color: #fff5f5;
    border-radius: 3px;
    padding-left: 5px;
    padding-right: 5px;
    color: #ff502c;
    margin: 0 3px;
    line-height: 1.1rem;
    font-size: .87rem;
  }
  iframe {
    width: 100% !important;
    height: 34rem;
    border: 1px solid #ccc;
    border-radius: 8px;
  }
  .hljs {
    display: block;
    overflow-x: auto;
    padding: 0;
    background: transparent !important;

    /*&::-webkit-scrollbar {
      width: 4px;
      height: 4px;
      background-color: #ffffff;
    }*/
  }
  .hljs-params, .hljs-class .hljs-title {
    color: #f8f8f2;
  }
  .hljs-keyword, .hljs-selector-tag, .hljs-name, .hljs-attr {
    color: #f92672 !important;
  }
  .hljs-string,
  .hljs-type,
  .hljs-built_in,
  .hljs-builtin-name,
  .hljs-selector-id,
  .hljs-selector-attr,
  .hljs-selector-pseudo,
  .hljs-addition,
  .hljs-variable,
  .hljs-template-variable {
    color: #e6db74;
  }
  .hljs-bullet, .hljs-quote, .hljs-number, .hljs-regexp, .hljs-literal, .hljs-link {
    color: #ae81ff;
  }
  .hljs-doctag {
    color: #d14;
  }
  .hljs-comment {
    color: #998;
    font-style: italic;
  }
  .hljs-tag .hljs-attr {
    color: #008080;
  }
}

/*文章右边内容*/
.article-side {
  margin-left: 30px;
  background-color: #fff;
}

// 文章目录
.article-catalog {
  &--title {
    padding: 10px 0;
    text-align: center;
    border-bottom: 1px solid #cccccc;
  }
}

.article-menu {
  .title-anchor {
    padding: 10px 0;
  }
}
